<template>
  <div class="daily_cont detail_cont">
    <div class="daily_write">
      <div class="cate_title">
        <img src="/images/dailylook_h1.png" alt="데일리룩타이틀">
      </div>

      <section>
        <h3>제목</h3>
        <input type="text" placeholder="데일리룩 제목을 입력해주세요.">
      </section>

      <section class="mt30">
        <h3>데일리룩 이미지</h3>
        <input type="file"
               id="file"
               ref="getFile"
               @change="imageUpload"/>
        <img src=""
             v-if="fileList.length"
             ref="preview"
             class="previewImg"
             alt="이미지미리보기">
      </section>

      <section>
        <h3>내용</h3>
        <textarea rows="10"></textarea>
      </section>

      <section>
        <div class="btn_wrap">
          <router-link to="/dailylook">
            <button class="button_default_border mr5">취소</button>
          </router-link>
          <button class="button_default_fill ">등록</button>
        </div>
      </section>
    </div>
  </div>
</template>

<script>
export default {
  name: "DailyWrite",
  data() {
    return {
      fileList: []
    }
  },
  methods: {
    imageUpload() {
      let file = this.$refs.getFile
      this.fileList = file.files // 선택된 파일의 목록이 FileList 객체 형태로 files 속성에 저장
      let reader = new FileReader()
      reader.readAsDataURL(this.fileList[0]);
      reader.onload = () => {
        //로컬 이미지를 보여주기
       this.$refs.preview.src = reader.result;
        //썸네일 이미지 생성
        let thumbImg = new Image() //drawImage 메서드에 넣기 위해 이미지 객체화
        thumbImg.src = reader.result  //data-url를 이미지 객체에 주입
      }
    },
  }
}
</script>

